@import url(https://use.typekit.net/jmk3xov.css);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
canvas {
  position: fixed;
  top:0;
  left:0;
}



:root {
  --dark-bg: rgba(15, 15, 15, 0.95);
  --spacing: 350px;
  --yellow: #f5c42f;
  font-family: 'Press Start 2P';
  font-weight: 400;
  font-style: normal;
  letter-spacing: 1.5px;
}

body{
  background-color: black;
}

main {
  width: 100vw;
  color: #f5c42f;
  color: var(--yellow);
  z-index: 99;
  position: absolute;
  width: 100%;
  margin: 0px auto;
  padding: 120px 0px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}


.btn {
	display: inline-block;
  position: relative;
	border: 0;
	background: #f5c42f;
	background: var(--yellow);
	color: darkblue;
	border-radius: 20px;
	padding: 8px 30px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 16px;
	margin-top: 10px;
  text-decoration: none;
    
}

.btn-mint{
  width: 42%;
  display: inline-block;
  position: relative;
  font-family: 'Press Start 2P';
	border: 0;
	background: #f5c42f;
	background: var(--yellow);
	color: black;
	border-radius: 20px;
	padding: 8px 30px;
	text-transform: uppercase;
	font-weight:lighter;
	font-size: 20px;
	margin-top: 10px;
  letter-spacing: 2.5px;
  text-decoration: none;
}

.btn-mint:hover{
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.btn:hover {
	-webkit-transform: scale(0.95);
	        transform: scale(0.95);
}

strong{
  color:#f5c42f;
  color:var(--yellow);
}

h1, h2, h3, blockquote {
  font-family: 'Press Start 2P';
  font-weight: lighter;
  font-style: normal;
  color: #f5c42f;
  color: var(--yellow)
}


  header {
    background: rgba(15, 15, 15, 0.95);
    background: var(--dark-bg);
    grid-column: 2 / span 5;
    font-size: 1.2rem;
    padding: 2.5rem;
    margin-bottom: 350px;
    margin-bottom: var(--spacing);
    border-radius: 15px;
  }

  header h1 {
    line-height: 45px;
  } 

  header p{
    padding-top: 25px;
    padding-bottom: 10px;
  }

  blockquote, section p {
    color: rgb(14, 204, 14);
    margin-top: 15px;
    font-weight: lighter;
  }

  section p:hover{
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1); 
  }
 

  header p {
    font-family: 'Press Start 2P';
    color: limegreen;
    letter-spacing: 0;
  }

  section {
    grid-column: 2 / 8;
    padding: 1rem;
    background: rgba(15, 15, 15, 0.95);
    background: var(--dark-bg);
    font-size: 1.15rem;
    line-height: 1.75;
    margin-bottom: 350px;
    margin-bottom: var(--spacing);
    font-family: 'Press Start 2P';
    border-radius: 15px;
    text-align: center;
    font-weight: bold;
  }

  section p, h3{
    margin-top: 10px;
    margin-bottom: 15px;
  }



  section h2,h3{
      text-shadow: 2px 2px rgb(117, 117, 28)
  }

 #community-img{
  
   width: 15%;
   height: 15%;
 }

  #nagmi{
    color: rgb(165, 60, 60);
  }

  #section-community h2, h3{
    display: flex;
    justify-content: center;
  }

  #section-community p {
    margin-bottom: 25px;
    text-align: center;
  }

  #section-community{
    padding-bottom: 21px;
  }
  
  blockquote {
    margin: 0;
    padding: 0;
    grid-column: 2 / span 9;
    margin-bottom: 350px;
    margin-bottom: var(--spacing);
  }

  .team-item img{
 
    width:25%;
    height: 100%;
    border-radius: 25px;
    border: 1.5px solid rgba(255, 255, 255, 0.3); 
  }

  .team-item{
   
    border-radius: 20px;
    position: relative;
    overflow: hidden;
  }



  blockquote p {
    color: limegreen;
    background-color: rgba(15, 15, 15, 0.95);
    font-size: 2rem;
    display: inline;
    letter-spacing: 4px;
    line-height: 1.2;
    
  }

  .left {
    grid-column: 6 / 12;
  }

  /* Footer */
.footer {
  position:-webkit-sticky;
  position:sticky;
	border-top: var(--text-color) 1px solid;
	padding: 30px;
	margin-top: 550px;
    width: 100%;
}

.footer ul {
	display: flex;
	align-items: center;
	justify-content: space-around;
	text-align: center;
}

.footer a:hover {

	color:chartreuse;
}
@media (max-width: 480px) {



  canvas {
    position: fixed;
    top:0;
    left:0;
  }

  
  header{
    position: absolute;
    margin: 15px 5px 5px -15px;
    left: 0px;
    right: -190px;

  }
  header h1{
    font-size: medium;
  }

  .btn-mint{
    font-size: small;
  }

  blockquote p{
    position:relative;
    font-size: large;
    top:300px;

  }

  section{
    position: relative;
    margin: auto;
    width: 350px;
  }
  section h3,p{
    font-size: smaller;
  }

  .left{
    position: relative;
    margin: auto;
    width: 350px;
    right:150px;
  }




}
.NavigationBarItems{
    background: transparent;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    width: 65%;
    cursor: pointer;
}

.NavigationBar-Logo{
    color: white;
    font-family: 'Press Start 2P';
    height: 80%;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-shadow: -1px 0 black, 0 2px black, 21px 0 transparent, 0 -1px black;

}
.NavigationBar-Logo:hover{
    color: #f5c42f;
}

#NavLogo {
    width: 75%;
    height: 100%;
    margin-left: 40px;

}

.nav-items{
    display: flex;
    grid-template-columns: repeat(5, auto);
    grid-gap: 35px;
    list-style: none;
    text-align: right;
    width: 70vw;
    justify-content: end;
    position: -webkit-sticky;
    position: sticky; 
    top: 0;

}

.NavigationBar-Logo{
    background: transparent;
}

.nav-links{

    color: white;
    font-family: 'Press Start 2P';
    font-weight: lighter;
    font-size: 1em;
    text-decoration: none;
    margin-top: 15px;
    text-shadow: -1px 0 black, 0 2px black, 21px 0 transparent, 0 -1px black;
}

#nav-docs{
    top:20px;
}



#collapsible-nav-dropdown{
    color: white;
}

#DropItem:hover{
    background: linear-gradient(transparent,transparent,  #476098);
    border-radius: 10px;
    transition: all 0.4s ease-out;

}

.nav-links:hover{

    color:#f5c42f;
}

@media screen and (max-width: 960px) {
    .nav-items{
        position: relative
    
    }
    
    .NavigationBarItems{
        font-size: x-small;

    }

    .NavigationBarItems.active{
        background: aliceblue;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        margin-left: 7px;
        margin-right: -75px;
    }

    .nav-links:hover{
        
        border-radius: 0;
    }

    .NavigationBar-Logo{
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%)
    }

    
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "coder";
  src: local("coder"),
    url(/static/media/PressStart2P-Regular.f2341734.woff) format("woff");
}

body {
  margin: 0;
  font-family: "coder", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

html {
  background-color: "#4C4C4C";
}

div {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}

:root {
  --yellow: #fccd35;
  --pink: #ff19e9;
  --green: #00cc00;
  --white: #ffffff;
  --light-grey: #b1b1b1;
  --dark-grey: #2b2b2b;
  --black: #000000;
}

